<template>
  <div class="monitor-list">
    <el-card>
      <div slot="header" class="card-title">
        <span>监控列表</span>
      </div>
      <el-form :inline="true">
        <el-radio-group v-model="productName" size="small" fill="#409eff">
          <el-radio-button label="卡卡贷"></el-radio-button>
          <el-radio-button label="豆豆钱"></el-radio-button>
        </el-radio-group>
        <el-select v-model="monitorType" clearable placeholder="全部">
          <el-option
            v-for="item in options1"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-select v-model="moduleType" clearable placeholder="全部">
          <el-option
            v-for="item in options2"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form>
      <el-table
        :data="msgList"
        stripe
        style="width: 100%"
        ref="multipleTable"
        :header-row-style="headerStyle"
      >
        <!-- <el-table-column type="selection" width="55"></el-table-column> -->
        <el-table-column label="变量" prop="variablename" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column label="模块" prop="modulename" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column
          label="类型"
          prop="monitorType"
          width="80"
          align="center"
          :formatter="formatter"
        ></el-table-column>
        <el-table-column label="红色预警" prop="redWarningNumber" width="120" align="center">
          <template slot-scope="scope">
            <router-link
              class="count"
              tag="span"
              :to="`/message-list?variableKey=${scope.row.variablekey}&productReportName=${scope.row.modulename}`"
            >{{scope.row.redWarningNumber}}</router-link>
          </template>
        </el-table-column>
        <el-table-column label="黄色预警" prop="yellowWarningNumber" width="120" align="center">
          <template slot-scope="scope">
            <router-link
              class="count"
              tag="span"
              :to="`/message-list?variableKey=${scope.row.variablekey}&productReportName=${scope.row.modulename}`"
            >{{scope.row.yellowWarningNumber}}</router-link>
          </template>
        </el-table-column>
        <el-table-column label="监控时间" prop="createtime" width="180" align="center"></el-table-column>
        <el-table-column label="操作" align="center" width="100">
          <template slot-scope="scope">
            <span class="btn-view" @click.stop.prevent="showConfirmFunc(scope.row)">删除</span>
            <span class="btn-view" @click.stop.prevent="lookup(scope.row)">详情</span>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        v-if="msgList.length"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="pageNo"
        :page-sizes="sizes"
        :page-size="pageSize"
        layout="prev, pager, next, sizes"
        :total="total"
      ></el-pagination>
    </el-card>

    <dialog-confirm @confirm="deleteFunc" :init-visible.sync="showDialogConfirm"></dialog-confirm>
  </div>
</template>

<script>
import dialogConfirm from '@/components/dialog-confirm';
import {getMonitorCenList, deleteVarMonitor} from '@/api/monitor.js';
export default {
  name: 'monitor-list',
  components: {dialogConfirm},
  data() {
    return {
      headerStyle: {
        color: '#409eff'
      },
      showDialogConfirm: false,
      productName: '卡卡贷',
      monitorType: undefined,
      moduleType: undefined,
      options1: [
        {
          value: 'credit',
          label: '授信'
        },
        {
          value: 'cash',
          label: '取现'
        },
        {
          value: 'none',
          label: '无'
        }
      ],
      options2: [
        {
          value: 1,
          label: '首贷'
        },
        {
          value: 2,
          label: '加贷'
        },
        {
          value: 3,
          label: '再贷'
        }
      ],
      pageNo: 1,
      pageSize: 10,
      total: 0, //列表总条数
      sizes: [10, 20, 50, 100], //select选项
      msgList: []
    };
  },
  watch: {
    productName: function() {
      this.getMsgList();
    },
    monitorType: function(newV) {
      if (newV === '') {
        this.monitorType = undefined;
      }
      this.pageNo = 1;
      this.pageSize = 10;
      this.getMsgList();
    },
    moduleType: function(newV) {
      if (newV === '') {
        this.moduleType = undefined;
      }
      this.pageNo = 1;
      this.pageSize = 10;
      this.getMsgList();
    }
  },
  created() {
    this.productName = this.$route.query.productName || '卡卡贷';
    this.monitorType = this.$route.query.monitorType;
    this.moduleType = this.$route.query.moduleType ? Number(this.$route.query.moduleType) : undefined;
    this.pageNo = this.$route.query.pageNo || 1;
    this.pageSize = this.$route.query.pageSize || 10;
    this.getMsgList();
    // TODO
  },
  methods: {
    getMsgList() {
      getMonitorCenList(this.productName, this.monitorType, this.moduleType, this.pageNo, this.pageSize)
        .then(result => {
          this.total = result.result.totalNum;
          this.msgList = result.result.items;
        })
        .catch(err => console.log(err));
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.pageNo = 1;
      this.getMsgList();
    },
    formatter(row) {
      let value = '';
      switch (row.monitorType) {
        case 'credit':
          value = '授信';
          break;
        case 'cash':
          value = '取现';
          break;
        case 'none':
          value = '无';
          break;
      }
      return value;
    },
    //当pageNo.改变的时候
    handleCurrentChange(pageNo) {
      this.pageNo = pageNo;
      this.getMsgList();
    },
    // 点击查看
    lookup(item = {}) {
      this.$store.commit('UPDATE_MONITORLISTPARAMS', {
        productName: this.productName,
        monitorType: this.monitorType,
        moduleType: this.moduleType,
        pageNo: this.pageNo,
        pageSize: this.pageSize
      });
      this.$router.push({
        name: 'univariate-monitor',
        query: {
          modulename: item.modulename,
          productname: item.productName,
          variablekey: item.variablekey,
          moduletype: item.expansion.moduletype,
          transmittype: item.expansion.transmittype,
          monitortype: item.monitorType
        }
      });
    },
    showConfirmFunc(item = {}) {
      this.showDialogConfirm = true;
      this.delItem = item;
    },
    deleteFunc() {
      this.showDialogConfirm = false;
      deleteVarMonitor(this.delItem.id)
        .then(result => {
          this.$message.success('删除成功');
          this.getMsgList();
        })
        .catch(err => console.log(err));
    },
    // 跳转监控中心详情
    goTo(item) {
      console.log(item);
    }
  }
};
</script>

<style lang="scss" scoped>
.el-card {
  margin: 20px 15px;
  padding-bottom: 20px;
}
form {
  margin-bottom: 20px;
  .el-select {
    margin-left: 20px;
  }
}

.btn-view,
.count {
  cursor: pointer;
  color: #409eff;
}
.btn-view + .btn-view {
  margin-left: 10px;
}
</style>
